import React, { Component } from "react";

class CartItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cartItemPrice: 0
    };
  }

  render() {
    //disabling menu items if they still show in cart if they're quantity is 0 or undefined
    var cartItemClass = "show";
    if (
      this.props.itemsInCart.quantity < 1 ||
      !this.props.itemsInCart.quantity
    ) {
      cartItemClass = "hide";
    } else {
      cartItemClass = "show";
    }
    return (
      <div className={"cartItem " + cartItemClass}>
        <h3>{this.props.itemsInCart.name}</h3>
        <div className="cartInfo">
          <p>Quantity: {this.props.itemsInCart.quantity}</p>
          <p>Subtotal: ${this.props.itemsInCart.price.toFixed(2)}</p>
        </div>
      </div>
    );
  }
}

export default CartItem;
