import React, { Component } from "react";
import BackendEntry from "./BackendEntry";
import BottomButton from "./BottomButton";

class Backend extends Component {
  render() {
    const buttonClass = "cartButton";
    return (
      <>
        <div id="refresh-contain">
          <button id="refresh-button" onClick={this.props.reloadItems}>
            <h2>Refresh</h2>
          </button>
        </div>

        <div id="backend-contain">
          {Object.keys(this.props.items).map(key => (
            <BackendEntry
              key={key}
              index={key}
              details={this.props.items[key]}
              updateItem={this.props.updateItem}
              deleteItem={this.props.deleteItem}
              updateItemsInCart={this.props.updateItemsInCart}
              itemsInCart={this.props.itemsInCart}
            />
          ))}

          <BottomButton className={buttonClass} />
        </div>
      </>
    );
  }
}

export default Backend;
